.main {
  margin: 0 auto;
  max-width: 890px;
}

.films-list {
  max-width: 900px;
  padding-top: 20px;
  margin: 0 auto;
}

.films-list li  {
  display: flex;
  padding: 0 65px 10px;
  font-size: 33px;
  margin-bottom: 20px;
  border-bottom: 1px solid;
  justify-content: space-between;
}

.box-inform {
  position: relative;
  width: 500px;
}

.destroy {
  position: absolute;
  right: 0;
  top: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
}

.destroy::before {
  content: "X";
  font-size: 30px;
  color: #fff;
}

.buttonClose {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
}

.buttonHide {
  background: brown;
  height: 40px;
  width: 90px;
  display: flex;
  justify-content: center;
}

.container {
  display: flex;
  justify-content: space-around;
  height: 30px;
  padding-bottom: 30px;
  border-bottom: 1px solid;
}

.container button {
  width: 90px;
  color: #fff;
  background: brown;
  font-size: 19px;
}

.add__new__films {
  width: 455px;
  position: -webkit-sticky;
  position: absolute;
  right: 276px;
  bottom: 85px;
  margin: 0 auto;
  background: #fff;
  box-sizing: border-box;
  z-index: 2;
  box-shadow: 0 0 10px #559453;
  padding: 30px;
  transition: .4s;
}

.add__new__films input {
  display: flex;
  margin: 15px 0;
  align-items: center;
  height: 40px;
  box-sizing: border-box;
  border: 1px solid #999;
  padding-left: 12px;
}